<script src="../../../AstroCoin-Frontend/src/store/index.js"></script>
<template>
  <div id="container">
    <div id="alert" class="error" :class="subscription ? 'active' : ''">
      <ion-icon name="warning" class="icon"></ion-icon>
      <div class="title">Вы не оформили подписку!</div>
      <div class="pay-btn" @click="subscription">Оформить</div>
    </div>
    <HeaderUserBar />

    <div id="banner">
      <div class="swiper banner-collage">
        <div class="swiper-wrapper">
          <div class="swiper-slide">
            <img src="../files/banner.png"  alt=""/>
            <div class="item-gradient"></div>
            <div class="item-content" v-if="">
              <div class="item-title">{{ title_1 }}</div>
              <div class="item-price">5000</div>
              <div class="item-currency">{{ desc_1 }}</div>
            </div>
          </div>
          <div class="swiper-slide">
            <img src="../files/banner-2.png"  alt=""/>
            <div class="item-gradient"></div>
            <div class="item-content">
              <div class="item-title">{{ title_2 }}</div>
              <div class="item-price">5000</div>
              <div class="item-currency">{{ desc_2 }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
<!--    <div id="main-tools">-->
<!--      <router-link tag="div" to="/terms" class="btn terms">-->
<!--        <ion-icon name="information-circle-outline" class="ai-icon"></ion-icon>-->
<!--        <span class="btn-text">Условия</span>-->
<!--      </router-link>-->
<!--    </div>-->
    <div id="service">
      <div class="service-title">{{ service }}</div>
      <div class="swiper service-cards">
        <div class="swiper-wrapper">
          <router-link tag="div" to="/qr-code-cashback" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/service-partners.svg" alt="Service Partners">
            </div>
            <div class="service-title">{{ cashback }}</div>
          </router-link>
          <router-link tag="div" to="https://prizes.gamee.com/game-bot/karatekid2" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/service-ticket-game.svg" alt="Service Partners">
            </div>
            <div class="service-title">Ticket Game</div>
          </router-link>
          <router-link tag="div" to="/participation" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/service-participate.svg" alt="Service Partners">
            </div>
            <div class="service-title">{{ participation }}</div>
          </router-link>
          <router-link tag="div" to="/give-a-dream" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/humans.svg" alt="Give a dream">
            </div>
            <div class="service-title">{{ give_a_dream }}</div>
          </router-link>
          <router-link tag="div" style="visibility: hidden;opacity: 0" to="/give-a-dream" class="service-card swiper-slide"></router-link>
        </div>
      </div>
    </div>

    <BottomNavigationMenu active="home" />
  </div>
</template>

<script>
import axios from 'axios'
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import HeaderUserBar from "@/components/HeaderUserBar";

export default {
  name: "Home",
  data: () => ({
    user_id: 0,
    user: null,
    subscription: false,
    title_1: null,
    desc_1: null,
    title_2: null,
    desc_2: null,
    service: null,
    cashback: null,
    participation: null,
    give_a_dream: null,
  }),
  components: {HeaderUserBar, BottomNavigationMenu},
  async mounted() {
    const telegram = window.Telegram.WebApp;
    const userData = telegram.initDataUnsafe.user;
    const mainButton = telegram.MainButton;
    const backButton = telegram.BackButton;

    // this.user_id = 5296876609
    this.user_id = userData.id

    backButton.hide()
    mainButton.hide()
    mainButton.hideProgress()


    new Swiper(".banner-collage", {
      effect: "cards",
      grabCursor: true,
      autoplay: {
        delay: 2500,
      },
      slidesPerView: 'auto',
      loop: true,
    });

    new Swiper(".service-cards", {
      watchSlidesProgress: true,
      slidesPerView: 3,
    });

    await axios.get('https://back.sombra.uz/api/user/' + this.user_id)
        .then(response => {
          this.user = response.data.data
        })
        .catch(error => console.log(error));

    if (!this.user.agreed) {
      this.$router.push('/terms')
    }

    if (this.user.lang === 'ru') {
      this.title_1 = 'Квартира за';
      this.desc_1 = 'сум';
      this.title_2 = 'Машина за';
      this.service = 'Другие сервисы';
      this.cashback = 'Кешбек';
      this.participation = 'Участвовать';
      this.give_a_dream = 'Подри мечту';
    } else {
      this.title_1 = 'Kvartira';
      this.desc_1 = 'so\'m evaziga';
      this.title_2 = 'Mashina';
      this.desc_2 = 'so\'m evaziga';
      this.service = 'Boshqa servislar';
      this.cashback = 'Keshbek';
      this.participation = 'Qatnashish';
      this.give_a_dream = 'Orzu ulash';
    }

  },
  methods: {
    subscription()  {
      window.Telegram.WebApp.sendData('subscription')
    }
  },
}
</script>

<style scoped>
/*#header {*/
/*  margin: 50px 23px 0;*/
/*}*/
</style>
